import ReactCardFlip from 'react-card-flip';
import { useState, useEffect } from 'react';
import { useTheme } from '@mui/material/styles';
import { Box, Grid, Card, CardActions, CardContent, IconButton, Button, MenuItem, TextField, Typography, Container } from '@mui/material';
import FavoriteIcon from '@mui/icons-material/Favorite';
import { red } from '@mui/material/colors';

const cardStyle = {
    minWidth: 250,
    minHeight: 350,
    boxShadow: 3
};

const bull = (
    <Box component="span" sx={{ display: 'inline-block', mx: '2px', transform: 'scale(0.8)' }}>
        •
    </Box>
);

const PhraseCard = (props) => {
    const [isFlipped, flip] = useState(false);
    const [isFavourite, makeCardFavourite] = useState(props.isFavourite);

    let invertFavStat = (currentNum) => {
        if (isFavourite == 'disable') {
            makeCardFavourite('error');
            addFavToStorage(currentNum);
        } else {
            makeCardFavourite('disable');
            deleteFavToStorage(currentNum);
            console.log('del', currentNum);
        }
    };

    let addFavToStorage = (num) => {
        let appProp = JSON.parse(localStorage.getItem('appProp'));
        let favStorage = appProp.favStorage;

        console.log('isFav');
        if (appProp.favStorage.indexOf(num) == -1) {
            favStorage.push(num);
            appProp.favStorage = favStorage;
            localStorage.setItem('appProp', JSON.stringify(appProp));
        }
    };

    let deleteFavToStorage = (num) => {
        let appProp = JSON.parse(localStorage.getItem('appProp'));
        let favStorage = appProp.favStorage;

        if (appProp.favStorage.indexOf(num) != -1) {
            favStorage.splice(appProp.favStorage.indexOf(num), 1);
            appProp.favStorage = favStorage;
            localStorage.setItem('appProp', JSON.stringify(appProp));
        }
    };

    let setCardChecked = (num) => {
        let appProp = JSON.parse(localStorage.getItem('appProp'));
        let checkedList = appProp.checkedList;
        console.log('checkList', checkedList);
        if (!checkedList) {
            checkedList = [];
        }
        console.log('checkListAfter', checkedList);
        console.log('cardChecked', num);

        if (checkedList.indexOf(num) == -1) {
            checkedList.push(num);
            appProp.checkedList = checkedList;
            localStorage.setItem('appProp', JSON.stringify(appProp));
        }
    };

    return (
        <>
            <ReactCardFlip isFlipped={isFlipped} flipDirection="horizontal">
                <Card sx={cardStyle}>
                    <Grid container justifyContent="space-between">
                        <Grid item>
                            <Box margin="10px">
                                <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                                    #{props.currentNum}
                                </Typography>
                            </Box>
                        </Grid>
                        <Grid item>
                            <IconButton
                                margin="10px"
                                size="small"
                                aria-label="add to favorites"
                                onClick={() => invertFavStat(props.currentNum)}
                            >
                                <FavoriteIcon color={isFavourite} />
                            </IconButton>
                        </Grid>
                    </Grid>
                    <CardContent
                        onClick={() => {
                            flip(true);
                            setCardChecked(props.currentNum);
                        }}
                        sx={{ height: '300px' }}
                    >
                        <Typography variant="h3" component="div" textAlign="center" sx={{ mt: 3 }}>
                            {props.phrase}
                        </Typography>
                        <Typography variant="body2" sx={{ mb: 3, mt: 3 }} textAlign="center">
                            {props.example}
                        </Typography>
                    </CardContent>
                </Card>
                <Card sx={cardStyle}>
                    <Grid container justifyContent="space-between">
                        <Grid item>
                            <IconButton
                                margin="10px"
                                size="small"
                                aria-label="add to favorites"
                                onClick={() => invertFavStat(props.currentNum)}
                            >
                                <FavoriteIcon color={isFavourite} />
                            </IconButton>
                        </Grid>
                        <Grid item>
                            <Box margin="10px">
                                <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                                    #{props.currentNum}
                                </Typography>
                            </Box>
                        </Grid>
                    </Grid>
                    <CardContent onClick={() => flip(false)} sx={{ height: '300px' }}>
                        <Typography variant="h3" component="div" textAlign="center" sx={{ mt: 3 }}>
                            {props.meaning}
                        </Typography>
                        <Typography variant="body2" sx={{ mb: 3, mt: 3 }} textAlign="center">
                            {props.translation}
                        </Typography>
                    </CardContent>
                </Card>
            </ReactCardFlip>
        </>
    );
};

export default PhraseCard;
