import PropTypes from 'prop-types';
import { useEffect, useState } from 'react';
import React from 'react';
import Carousel from 'react-material-ui-carousel';
import { useLocation } from 'react-router-dom';

// material-ui
import { Box, Grid, Card, CardActions, CardContent, IconButton, Button, MenuItem, TextField, Typography, Container } from '@mui/material';
import FavoriteIcon from '@mui/icons-material/Favorite';

// project imports
import PhraseCard from './PhraseCard';
import SubCard from 'ui-component/cards/SubCard';
import MainCard from 'ui-component/cards/MainCard';
import SecondaryAction from 'ui-component/cards/CardSecondaryAction';
import { gridSpacing } from 'store/constant';
import { CircularProgress } from '@mui/material';
import ReactCardFlip from 'react-card-flip';
import readExcelFile from './ExcelReader';

let checkFavCard = (num) => {
    let appProp = JSON.parse(localStorage.getItem('appProp'));
    // console.log('appProp', appProp);
    if (appProp == null) {
        localStorage.setItem('appProp', JSON.stringify({ favStorage: [], checkedList: [] }));
        appProp = JSON.parse(localStorage.getItem('appProp'));
    }

    let output = 'disable';

    if (appProp.favStorage.indexOf(num) != -1) {
        output = 'error';
    }
    // console.log('appProp', appProp, num, output);
    return output;
};

let cardAdapter = (num, slidingMode, state, updateState) => {
    let output = 0;
    if (slidingMode && num == state.to % 20) {
        output = num;
        updateState({
            from: state.from + 20,
            to: state.to + 20,
            currentNum: state.currentNum,
            slideMode: true
        });
    } else {
        output = num % 20;
    }

    return output;
};

// ===============================|| UI COLOR ||=============================== //

const UIColor = () => {
    let location = useLocation();
    let stateLocation = location.state;
    let [state, updateState] = useState(stateLocation);
    console.log('From color befor', state);

    if (!state) {
        state = {
            from: 0,
            to: 20,
            currentNum: 0
        };
    }

    // if(state.slideMode) {
    //     state = {
    //         from: 0,
    //         to: 20,
    //         currentNum: 0
    //     };
    // }

    console.log('From color after', state);

    let cardList = [];
    let phraseList = [];

    if ((state.from || state.from === 0) && state.to) {
        phraseList = readExcelFile(state.from, state.to);
    } else if (state.customList) {
        phraseList = state.customList.map((item) => item.cardInfo);
    }

    for (let index = 0; index < phraseList.length; index++) {
        cardList.push(
            <Grid item key={index}>
                <Grid container justifyContent="center">
                    <Grid item sx={{ width: '250px' }}>
                        <PhraseCard
                            phrase={phraseList[index]['English phrase ']}
                            example={phraseList[index].Example}
                            translation={phraseList[index]['Russian translation']}
                            meaning={phraseList[index]['Meaning of the phrase ']}
                            currentNum={state.from !== null ? state.from + index + 1 : state.customList[index].num}
                            isFavourite={
                                state.from !== null ? checkFavCard(state.from + index + 1) : checkFavCard(state.customList[index].num)
                            }
                        ></PhraseCard>
                    </Grid>
                </Grid>
            </Grid>
        );
    }

    return (
        <>
            <MainCard title="Collocation training">
                <Grid container sx={{ height: '530px' }} spacing={gridSpacing} justifyContent="center" alignItems="center">
                    <Carousel
                        sx={{ width: '500px' }}
                        animation="slide"
                        autoPlay={false}
                        index={state.currentNum}
                        next={(now, prev) => {
                            cardAdapter(now, state.slideMode, state, updateState);
                        }}
                    >
                        {cardList}
                    </Carousel>
                </Grid>
            </MainCard>
        </>
    );
};

export default UIColor;
